import React from 'react';
import {browserHistory} from 'react-router';
import $ from 'jquery';
import PaginationTable from '../PaginationTable';
require('styles/App.css');


class Mngr extends React.Component {

  constructor(){
    super();
    this.state = {
			data:[],
			BKCODE: '',
      CLRBKCDE: '',			
    }
    this.handleClick=this.handleClick.bind(this);
	this.handleQuery=this.handleQuery.bind(this);
  }


handleQuery(){
	  var orgname=sessionStorage.getItem('orgname');
		var param={ORGNAME:orgname,BKCODE:this.state.BKCODE,CLRBKCDE:this.state.CLRBKCDE,TXNDAT:this.state.txndat};
		var _this=this;
			window.post('MonitorQuery',param,function(data){
					 var list=JSON.parse(data);
           _this.setState({
           data: list
          });
	  });
}
 handleClick(e){

	let msg = e.target.alt;
	let instrid = msg.substring(0,msg.indexOf('index'));
	let index = msg.substring(msg.indexOf('index')+5);
	let Record = this.state.data[index].Record;
	sessionStorage.setItem('INSTRID',instrid);
	sessionStorage.setItem('ACTNOFROM',Record.actnofrom);
	sessionStorage.setItem('CLRBKCDE',Record.clrbkcde);
	sessionStorage.setItem('ACTNOTO',Record.actnoto);
	sessionStorage.setItem('BKCODE',Record.bkcode);
	sessionStorage.setItem('TXAMT',Record.txamt);
	sessionStorage.setItem('CURCDE',Record.curcde);
}

handleQueryChange(e){
    const target = e.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState(
      {
        [name]: value
      });
}


render() {
	
    return (
    <div className='content'>
 <div className='form-inline'>
			<div className='head'></div>
		

						<div className='form-group' style={{marginLeft:'30px'}}>
								<label  htmlFor='exampleInputName2'  className='querylabel'></label>
								<input name='BKCODE'  type='text'  className='form-control'style={{width:'40%',marginLeft:'0px'}}
								onChange = {e => this.handleQueryChange(e)}></input>
						</div>
						<div className='form-group'style={{marginLeft:'0px'}}>
								<label  htmlFor='exampleInputName2'  className='querylabel'></label>
								<input name='CLRBKCDE'  type='text'  className='form-control'style={{width:'40%',marginLeft:'0px'}}
								onChange = {e => this.handleQueryChange(e)}></input>
						</div>
						<div className='form-group'style={{marginLeft:'0px'}}>
								<label  htmlFor='exampleInputName2'  className='querylabel'></label>
								<input name='txndat'   type='date'   className='form-control'style={{width:'65%',marginLeft:'0px'}}
								onChange = {e => this.handleQueryChange(e)}></input>
						</div>
							<button  className='btn btn-default' style={{marginLeft:'20px',marginTop:'0px'}}
							onClick={this.handleQuery}></button>
					</div>		
	<div className='line'></div>


     <PaginationTable data ={this.state.data} onClick={this.handleClick}
		  columns={['txid','bkcode','actnofrom','clrbkcde','actnoto','txamt','curcde','txndat','txntime','compst']}
      header = {['ID','','','','','','','','','']} />
	</div>
    );
  }
}



export default Mngr;


