import React from 'react';
import {browserHistory} from 'react-router';
import PaginationTable from '../PaginationTable';
require('styles/App.css');

class Vote extends React.Component {
  constructor(){
    super();
    this.state = {
      data:[]
    }
 
				this.handleQuery=this.handleQuery.bind(this);
	
	}
			 	componentWillMount(){
	   	  	var _this=this;
				  	window.post('QueryVoteOpinion',{},function(data){
           			_this.setState({
					 					data: data
								});
	    			});
  			}		  

handleQuery(e){
			var _this=this;
			var target = e.target;

      var record = JSON.parse(target.dataset.record);
			var VoteId = record.Voteid;
      var RuleId = record.Ruleid;

			var buttonvalue=e.target.dataset.value;
			var param={Voteid:VoteId,Opinion:buttonvalue,RuleId:RuleId}; 

			window.post('Vote',param,function(data){
						window.post('QueryVoteOpinion',{},function(data){
									_this.setState({
										data: data
									});
	  				});
			});
}



 render() {
  return (
    <div className='content'>
		<div className='head'></div>
    <div className='head_shanglian'></div>
		<div className='line'></div>
     <PaginationTable data ={this.state.data} onClick={this.handleQuery}  
		  columns={['DeadlineDate', 'InitiateOrganization', 'OrganizationName']}  
			extraColumnMap={[{"Opinion.W":[<button value='Y'></button>,<button value='N'></button>]},
											 {"Opinion.Y":""},
											 {"Opinion.N":""},
											 {"Opinion.D":""},
											 {"Opinion.S":""}]}
      header = {['','','','']} />
		</div>
  );
  }
}


export default Vote;


