require('normalize.css/normalize.css');
require('styles/App.css');
import React from 'react';
import {browserHistory} from 'react-router';
class LoginMngr extends React.Component {

  constructor(){
    super();
    this.state = {
      username:'gov',
      password:'123456'
    }
  }

 handleSubmit() {
  let values =  this.state;
  let param={username:values.username,password:values.password,usertype:'4'};
  window.post('login',param,function(data){
    if(!data.success){
      alert(data.message);
      return;
    }
    sessionStorage.setItem('token', data.message);
    sessionStorage.setItem('usertype','4');
    sessionStorage.setItem('orgname',data.bank);
    sessionStorage.setItem('username',values.username);
    window.location.href='/monitor';
    //browserHistory.push('monitor');
  });
}

handleChange(e){
  this.setState(
    {
      usertype :e.target.value
    });
}

handleusername(e){
  this.setState(
    {
      username :e.target.value
    });
}

handlePassword(e){
  this.setState(
    {
      password :e.target.value
    });
}

  render() {
    return (
      <div>
        <h2>NOSTRO-VOSTRO Blockchain Regulator Login</h2>
        <div className='login'>
          <table>
          <tbody>
          <tr>
            <th></th>
            <td>
              <input type='text' id='username' placeholder='/'
                onChange = {e => this.handleusername(e)} value='gov'/>
            </td>
          </tr>
          <tr>
            <th>&nbsp;</th>
            <td>
              <input type='password' id='password' placeholder='' value='123456'
              onChange = {e => this.handlePassword(e)}/>
            </td>
          </tr>
          <tr>
            <td colSpan='2' style={{textAlign : 'center',marginLeft:'20px'}}>
              <button type='button' onClick ={this.handleSubmit.bind(this)}>&nbsp;</button>&nbsp;
              <button style={{marginLeft:'40px'}}></button>
            </td>
          </tr>
          </tbody>
        </table>
	    </div>
    </div>
    );
  }
}


export default LoginMngr;



