import React from 'react';
import {browserHistory} from 'react-router';
import PaginationTable from '../PaginationTable';
import ReactDOM from 'react-dom'
require('styles/App.css');

class Pubkeymanager extends React.Component {
  constructor(){
    super();
    this.state = {
      data:[]
    }
  }

 componentWillMount(){

		var _this=this;
    var usertype=sessionStorage.getItem('usertype');
  	var orgname=sessionStorage.getItem('bkCode');
		var param={ROLE:'Bank',ORGNAME:orgname,USERTYPE:usertype};
     window.post('QueryPubKey',param,function(data){
          var list=JSON.parse(data);
					if(!list[0]){
						return;
          }
          console.log(data);
          console.log(JSON.parse(data));
           _this.setState({
           data: list
          });
	  });
  }

upload(){
  	var _this=this;
    var uploadForm = ReactDOM.findDOMNode(this.refs.uploadForm);
    var usertype=sessionStorage.getItem('usertype');
  	var orgname=sessionStorage.getItem('bkCode');
var formData = new FormData(uploadForm);
    formData.append("ROLE",'Bank');
    formData.append("ORGNAME",orgname);
    formData.append("USERTYPE",usertype);
window.formPost('UploadPubKey',formData,function(data){
alert(data);
        var param={ROLE:'Bank',ORGNAME:orgname,USERTYPE:usertype};
    		 window.post('QueryPubKey',param,function(data){
          var list=JSON.parse(data);
					if(!list[0]){
						return;
          }
           _this.setState({
           data: list
          });
	  });
});
}


render() {
  return (
    <div className='content'>
		<div className='head'></div>
    <div className='head_shanglian'></div>
		<div className='line'></div>
        	 <div className='form-inline'>
       <form action="#" method="post" name='uploadForm' encType="multipart/form-data" ref='uploadForm'>
                <div className="form-group">
                    <input type="file" name="uploadFile" className="btn btn-default" />
                </div>
                    <input name='upload' type='button' onClick={this.upload.bind(this)} value='' className="btn btn-default" />
                </form>
</div>
      <PaginationTable data ={this.state.data} onClick={this.handleClick}  
		  columns={['Pubid','publicName','privateName', 'Date', 'Status']}  
      header = {['ID','','','','']} /> 
  </div>
  );
  }
}

export default Pubkeymanager;



