import React from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router';
import bank from './../images/bank.png';
import user from './../images/user.png';

class Header extends React.Component {
  
  render() {
       
return (
    <div>
      <nav className='navbar navbar-inverse 'style={{margin:'0 auto'}}>
            <div className='container'>
              <div className='navbar-header'>
                {this.props.usertype == '2' ? 	<img src={bank} className='imageicon' /> : 	<img src={user} className='imageicon' />}
                <span className='navbar-brand'>{this.props.usertype == '2' ? '' : ''}</span>
                <span className='navbar-brand'>{this.props.username}</span>
              </div>
              <div id='navbar' className='navbar-collapse collapse' style={{marginTop:'15px'}}>
                <ul className='nav navbar-nav navbar-right' >
                  {(() => {switch (this.props.usertype) {
                       case '1':
                       return <div>
                       <Link to='/user/input' className='headNav'></Link>&nbsp;
                       <Link to='/user/inquery' className='headNav'></Link>&nbsp;
                       <Link to='/user/outquery' className='headNav'></Link>
                       <Link to='/logout' className='headNav'></Link>
                    </div>

                      case '2':
                        return <div>
                      <Link to='/bank/inquery' className='headNav'><font style={{hoverColor:'#fff'}}></font></Link>&nbsp;
                      <Link to='/bank/outquery'className='headNav' ></Link>&nbsp;
                      <Link to='/bank/nostro' className='headNav'>NOSTRO</Link>
                      <a href='../page/BankShowChain.html'  className='headNav'></a>
                      <Link to='/logout' className='headNav'></Link>
                    </div>
                    
                      case '3':
                         return <div>
                       <Link to='/bank/regulate' className='headNav'></Link>&nbsp;
                       <Link to='/bank/vote' className='headNav'></Link>
                       <Link to='/bank/pubkeymanager' className='headNav'></Link>
                       <Link to='/bank/keymanager' className='headNav'></Link>
                       <Link to='/logout' className='headNav'></Link></div>
                      
                      case '4':
                         return <div>
                       <Link to='/monitor/regulate' className='headNav' ></Link>&nbsp; 
                      <Link to='/monitor/query' className='headNav'></Link>&nbsp;
                        <Link to='/monitor/pubkeymanager' className='headNav'></Link>
                      <Link to='/logout' className='headNav' ></Link></div>
                    }})()}

                   {/* {this.props.usertype == '2' ?
                    <div>
                      <Link to='/bank/inquery' className='headNav'><font style={{hoverColor:'#fff'}}></font></Link>&nbsp;
                      <Link to='/bank/outquery'className='headNav' ></Link>&nbsp;
                      <Link to='/bank/nostro' className='headNav'>NOSTRO</Link>
                       <Link to='/bank/nostro' className='headNav'></Link>&nbsp;
                       <Link></Link>
                      <a href='../page/BankShowChain.html'  className='headNav'></a>
                      <Link to='/logout' className='headNav'></Link>
                    </div>
                  :
                  <div>
                      <Link to='/user/input' className='headNav'></Link>&nbsp;
                      <Link to='/user/inquery' className='headNav'></Link>&nbsp;
                      <Link to='/user/outquery' className='headNav'></Link>
                      <Link to='/logout' className='headNav'></Link>
                  </div>
                } */}
                </ul>
              </div>
            </div>
          </nav>
    </div>
    );
  }
}

export default Header;



