require('normalize.css/normalize.css');
require('styles/App.css');
import React from 'react';
import {browserHistory} from 'react-router';
class LoginBank extends React.Component {

  constructor(){
    super();
    this.state = {
      password:'123456',
      username:'',
      usertype:''
    }
  }

 handleSubmit() {
  let values =  this.state;
  let param={username:values.username,password:values.password,usertype:values.usertype};
  window.post('login',param,function(data){
    if(!data.success){
      alert(data.message);
      return;
    }
    sessionStorage.setItem('token', data.message);
    sessionStorage.setItem('usertype',values.usertype);
    sessionStorage.setItem('username',values.username);
    sessionStorage.setItem('bkCode',data.bank);
    //browserHistory.push('bank');
    if(values.usertype =='2') {
      window.location.href='/bank';
    }else{
      window.location.href='/bank/regulate';
    }
  });
}

handleChange(e){
  const value = e.target.value;
  this.setState(
    {
      usertype :value
    });
}

handleusername(e){
  this.setState(
    {
      username :e.target.value
    });
}

handlePassword(e){
  this.setState(
    {
      password :e.target.value
    });
}

  render() {
    return (
      <div>
        <h2>NOSTRO-VOSTRO Blockchain Bank Login</h2>
        <div className='login'>
          <table>
          <tbody>
          <tr>
            <th></th>
            <td>
                  {this.state.usertype == '2' ?
                <select  name='username' className='user-input' onChange={this.handleusername.bind(this)} value={this.state.username}>
                <option value="" disabled>----</option>
                <option value ="user_a">user_a</option>
                <option value ="user_b">user_b</option>
              </select>
                :
               <select  name='username' className='user-input' onChange={this.handleusername.bind(this)} value={this.state.username}>
                <option value="" disabled>----</option>
                <option value ="admin_a">admin_a</option>
                <option value ="admin_b">admin_b</option>
              </select>
              }
            </td>
          </tr>
          <tr style={{marginTop:'50px'}}>
            <th>   &nbsp;   </th>
            <td>
              <input type='password' id='password' placeholder=''
              onChange = {e => this.handlePassword(e)} value={this.state.password}/>
            </td>
          </tr>
          <tr>
            <td colSpan='2' style={{textAlign : 'center',marginLeft:'30px',color:'pink'}}>
             <input type='radio'  name="usertype" value='2' 
              onClick={this.handleChange.bind(this)} /> 
            <input type='radio'  name="usertype" value='3'
             onClick={this.handleChange.bind(this)}  />  
             </td>
            </tr>
           
          <tr>
            <td colSpan='2' style={{textAlign : 'center',marginLeft:'30px'}}>
              <button type='button' onClick ={this.handleSubmit.bind(this)} style={{marginTop:'10px'}}>&nbsp;</button>&nbsp;
              <button style={{marginLeft:'40px',marginTop:'10px'}}></button>
            </td>
          </tr>
          </tbody>
        </table>
	    </div>
    </div>
    );
  }
}


export default LoginBank;



