import React from 'react';
import {browserHistory} from 'react-router';
import $ from 'jquery';
import Table from '../Table';
require('styles/App.css');


class MngrRegulate extends React.Component {

constructor(){
    super();
    this.state = {
			data:[],
			banks:{}
		}
		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleMoniterInput = this.handleMoniterInput.bind(this);
  }

	componentWillMount(){
		var _this  = this;
		 window.post('QueryBank',{},function(data){	
				 console.log(data);
           _this.setState({
           data: data
          });
	  });
  }

	drawTable(data){
		var trItems = [];
		var tdItems = [];
		if (data.length % 2 == 1){
				data.push('');
			}

		for (var index in data) {
			if(data[index] == ''){
				tdItems.push(<td key={'td'+index}></td>);
			}else{
			 tdItems.push(<td key={'td'+index}>
				<div className='tb-title' style={{marginLeft: '70px'}}>{data[index]}</div> <input ref='banks'
				type='checkbox' name='bank' value={data[index]} className='user-input' onChange={this.handleInputChange}/>
			</td>);
			}
			if (index % 2 == 1 && index != 0){
					trItems.push(<tr key={'tr'+index} className='tabtr'>{tdItems}</tr>);
					tdItems=[];
			}
		}
		return trItems;
	}

	handleInputChange(e){
    	const target = e.target;
		const value =  target.value;
		const checked =  target.checked;
		var obj=this.state.banks;
		Object.defineProperty(obj,value,{value:checked,configurable:true,enumerable:true});
	}

	handleMoniterInput(e){
		const target = e.target;
		const value = target.value;
		const name = target.name;
		this.setState(
		  {
			[name]: value
		  });
		
	}

	handleSubmit() {
		var orgname=sessionStorage.getItem('bkCode');
		var selectedBank='';
		var banksObj = this.state.banks;
		for(var key in banksObj){
			if(banksObj[key]){
				selectedBank+=key+',';
			}
		}
		selectedBank = selectedBank.substr(0,selectedBank.length-1);
		var param={OrganizationName:this.state.monitor,Opinion:'C',InitiateOrganization:orgname,RuleID:'ruleid01',BanktoMonitor:selectedBank};
		console.log(param);
		window.post('Creatvote',param,function(data){
			alert(data);
		});
}

   render() {
	
    return (
    <div className='content'>
			<div className='head'></div>
			<div className='line'></div>
            <table className='table_jianguan'>
            <tbody>
			<tr>
				<td>
					<div className='tb-title' style={{marginLeft: '70px'}}></div> <input
					type='text' name='monitor' className='user-input' onChange={this.handleMoniterInput} />
				</td>
			</tr>
			<tr>
				<td className='tb-title' style={{marginTop:'40px',marginLeft: '70px'}}>
				</td>
				</tr>
				<tr className='tabtr'> 
                    <td>	
                    </td>
                </tr>
      
				{this.drawTable(this.state.data)}
			</tbody>
            </table>
            <div className='submitbt'>
			<button className='button' onClick={this.handleSubmit}>&nbsp;&nbsp;</button>
		</div>
	</div>
    );
  }
}


export default MngrRegulate;


