import React from 'react';
import PaginationTable from '../PaginationTable';
require('styles/App.css');

class KeyManager extends React.Component {
  constructor(){
    super();
    this.state = {
      data:[]
		}
  }

  componentWillMount(){
       var _this=this;
       var bkcode =sessionStorage.getItem('bkCode')
       var param = {BKCODE:bkcode}
				  window.post('secretQuery',param,function(data){
          console.log(data);
           _this.setState({
					 data: data
					});
      });
     
	}
  
SecretUpdate(e){
      var _this=this;
      var target = e.target;
      var record = JSON.parse(target.dataset.record);
			var SecretKeyId = record.id;
      
			var param={id:SecretKeyId}; 
   
			window.post('secretUpdate',param,function(data){	
           var bkcode =sessionStorage.getItem('bkCode')
      var paramquery = {BKCODE:bkcode}
        window.post('secretQuery',paramquery,function(data){
									_this.setState({
										data: data
									});
	  				});			
      });

    
}

 render() {
  return (
    <div className='content'>
		<div className='head'></div>
    <div className='head_shanglian'></div>
		<div className='line'></div>
    <PaginationTable data ={this.state.data} onClick={this.SecretUpdate.bind(this)}  
		  columns={[ 'id', 'banks','scrtKey']}  	extraColumnMap={[{"state.Y":[<button value='N'></button>]},
                                                               {'state.N':''}
                                                             ]}
      header = {['ID','','','']} /> 
   </div>
  );
  }
}


export default KeyManager;


